	module.exports = {"pathContext":{"docHtml":"<h1 id=\"default-workflow-spec\"><a href=\"#default-workflow-spec\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Default Workflow Spec</h1>\n<p><img src=\"/argo/assets/alpha.svg\" alt=\"alpha\"></p>\n<blockquote>\n<p>v2.7 and after</p>\n</blockquote>\n<p>It's possible to set default workflow specs which will be written to all workflows if the spec of interest is not set. This can be configurated through the\nworkflow controller config <a href=\"../workflow/config/config.go#L11\">map</a> and the field <a href=\"../workflow/config/config.go#L69\">DefaultWorkflowSpec</a>. </p>\n<p>In order to edit the Default workflow spec for a controller, edit the workflow config map: </p>\n<pre><code class=\"language-bash\">kubectl edit cm/workflow-controller-configmap\n</code></pre>\n<p>As an example the time for a argo workflow to live after finish can be set, in the spec this field is known as <code>secondsAfterCompletion</code> in the <code>ttlStrategy</code>. Example of how the config map could look with this filed can be found <a href=\"./workflow-controller-configmap.yaml\">here</a>.</p>\n<p>In order to test it a example workflow can be submited, in this case the <a href=\"../examples/coinflip.yaml\">coinflip example</a>, the following can be run:</p>\n<pre><code class=\"language-bash\">argo submit ./examples/coinflip.yaml\n</code></pre>\n<p>to verify that the the defaultd are set run </p>\n<pre><code class=\"language-bash\">argo get [YOUR_ARGO_WORKFLOW_NAME]\n</code></pre>\n<p>You should then see the field, Ttl Strategy populated</p>\n<pre><code class=\"language-yaml\">Ttl Strategy:\n  Seconds After Completion:  10\n</code></pre>","docPath":"argo/default-workflow-specs","proj":"argo"}}

